export type EquipmentType = '' | ''
export type ArmorType     = '' | '' | '' | '' | ''
export type WeaponType    = '' | '' | '' | '' | '' | '' | '' | '' | '' | '' | '' | '' | '' | ''

export const ARMOR_TYPES:              ArmorType[]  = ['', '', '', '', '']
export const WEAPON_TYPES:             WeaponType[] = ['', '', '', '', '', '', '', '', '', '', '', '', '', '']
export const BLADEMASTER_WEAPON_TYPES: WeaponType[] = ['', '', '', '', '', '', '', '', '', '', '']
export const GUNNER_WEAPON_TYPES:      WeaponType[] = ['', '', '']


export function isBlademaster(weaponType: WeaponType) {
  return BLADEMASTER_WEAPON_TYPES.includes(weaponType)
}
